@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap);
*{
  margin: 0;
  border: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #0a0a0a;
  --color-bg-variant: #443e3e6c;
  --color-primary: #1664da;
  --color-primary-variant: rgb(10, 80, 161);
  --color-white: #fff;
  --color-light: rgb(219, 213, 213); 

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background: #0a0a0a;
  background: var(--color-bg);
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/static/media/bckgrnd.80d3d763.jpg);
  background-repeat: repeat;
  background-size: .2%;
}

/* ------------------- General Styles ---------------*/
.container{
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
  ;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 8rem;
  /* height: 100vh; */
}

section>h2, section>h5{
  text-align: center;
  color: rgb(219, 213, 213);
  color: var(--color-light);
}

section>h2{
  color: #1664da;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color: rgb(219, 213, 213);
  color: var(--color-light);
}

a{
  color: #1664da;
  color: var(--color-primary);
  transition:all 400ms ease;
  transition:var(--transition);
}

a:hover{
  color: #fff;
  color: var(--color-white);
}

.btn{
   width: -webkit-max-content;
   width: max-content;
   display: inline-block;
   color: #1664da;
   color: var(--color-primary);
   padding: .75rem 1.2rem;
   border-radius: .4rem;
   cursor: pointer;
   border: 1px solid #1664da;
   border: 1px solid var(--color-primary);
   transition: all 400ms ease;
   transition: var(--transition);
}

.btn:hover{
  background-color: #fff;
  background-color: var(--color-white);
  color: #0a0a0a;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background: #1664da;
  background: var(--color-primary);
  color: #0a0a0a;
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}


/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
  .container{
    width: 86%;
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
@media screen and (max-width: 600px) {
 
  .container{
    width: var(--container-width-ms);
  }

  section>h2 {
    margin-top: 2rem;
  }
}
header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    padding-bottom: 50rem;
}

.container{
    text-align: center;
}

.header__contaiainer{
    text-align: center;
    height: 100%;
    position: relative;
}

/* ----------------------------CTA (Call To Action) Button-------------------------------- */
.cta {
    margin: 2.5rem;
    display: flex;
    grid-gap: 1.2rem;
    gap: 1.2rem;
    justify-content: center;
}

/* ----------------------------Header Socials-------------------------------- */
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: .8rem;
    gap: .8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ----------------------------Me Image-------------------------------- */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

/* ----------------------------SCROLL DOWN-------------------------------- */
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    -webkit-transform: rotate(90%);
            transform: rotate(90%);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
    .header{
        height: 68vh;
    }
  
    section {
      margin-top: 6rem;
    }
  }
  
  /* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
  @media screen and (max-width: 600px) {
    .header{
        height: 100vh;   
    }

    .header__socials, .scroll__down{
        display: none;
    }
  }





nav{
    background: rgba(0, 0, 0, 0.384);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: .8rem;
    gap: .8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: .9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}

nav a:hover{
    background: rgba(211, 213, 218, 0.836);
    color: rgb(2, 24, 49);
}

