.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    grid-gap: 15%;
    gap: 15%;
    width: var(--container-lg);
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2.3rem;
    /* background: linear-gradient(45deg, transparent, var(--color-primary), transparent); */
    display: grid;
    place-items: center;
    
}

.about__me-img{
    border-radius: 2.3rem;
    overflow: hidden;
    -webkit-transform: rotate(15deg);
            transform: rotate(15deg);
    transition: var(--transition) ease-out;
}

.about__me-img:hover{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
    transition: 3s;
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    /* margin: 0 5rem 0 0; */
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: default
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: .95rem;
}

.about__card small{
    font-size: .7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}


/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
    .about__container{
        grid-template-columns: 1fr;
        grid-gap: 0;
        gap: 0;
    }
  
    .about__me {
      width: 50%;
      margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem 1.5rem;
    }

    .about__me-img{
        -webkit-transform: none;
                transform: none;
    }
  }
  
  /* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
  @media screen and (max-width: 600px) {
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;   
    }

    .about__cards{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;

    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1.5rem 0;
    }

    .about__me-img{
        -webkit-transform: none;
                transform: none;
    }
  }
.experience__container{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div{
    background: var(--color-bg-variant);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.experience__container > div:hover{
    background-color: transparent;
    border-color: var(--color-primary-variant);
    cursor: default;
}

.experience__container > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}

.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 2rem;
    row-gap: 2rem;
}

.experience__details{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
}

.experience__details-icon{
    margin-top: 6px;
    color: var(--color-primary);
}

/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
   .experience__container{
       grid-template-columns: 1fr;
   }
   .experience__container >div{
       width: 80%;
       padding: 2rem;
       margin: 0 auto;
   }

   .experience__content{
       padding: 1rem;
   }
   .container{
       width: var(--container-width-md);
   }
  }
  
  /* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
  @media screen and (max-width: 600px) {
    .experience__container{
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container >div{
        width: 100%;
        padding: 2rem 1rem;
    }
    .container{
        width: var(--container-width-sm);
    }
  }

.services__container{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 3rem;
    gap: 3rem;
}

.service{
    background: var(--color-bg-variant);
    border-radius: 0 0 2rem 2rem;
    border: 1px solid transparent;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    transition: var(--transition);
}

.service:hover{
    background: transparent;
    cursor: default;
    border-color: var(--color-primary-variant);
}

.service__head{
    background: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
    box-shadow: 0 2rem rgba(0, 0, 0, 0.103);
}

.service__head> h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}

.service__list{
    padding: 2rem;
}


.service__list li{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: .8rem;
}

.service__list-icon{
    color: var(--color-primary);
    margin-top: 2px;
}
.service__list p{
    font-size: .9rem;
}

/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
  .services__container{
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      gap: 2rem;
  }
  .service{
      height: auto;
  }
}

/* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
@media screen and (max-width: 600px) {
    .services__container{
        grid-template-columns: 1fr;
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }
}
.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2.5rem;
    gap: 2.5rem;
}

.portfolio__item{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
}

.portfolio__item:hover{
    border-color: var(--color-primary-variant);
    background: transparent;   
}

.portfolio__item-img{
    border-radius: 1.5rem;
    overflow: hidden;
}

.portfolio__item h3{
    margin: 1.2rem 0 2rem;
}

.portfolio__item-cta{
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
  .portfolio__container{
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 1.2rem;
      gap: 1.2rem;
  }
}

/* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
@media screen and (max-width: 600px) {
    .portfolio__container{
        grid-template-columns: 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}
.contact__container.container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    grid-gap: 12rem;
    gap: 12rem;
}

.contact__options{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

.contact__option{
    background: var(--color-bg-variant);
    padding: 1.2rem;
    border-radius: 1.2rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
}

.contact__option:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon{
    font-size: 1.5rem;
    margin-bottom: .5rem;
}
.contact__option a{
    margin-top: .7rem;
    display: inline-block;
    font-size: .8rem;
}


/* ############################  FORM  ################################### */
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.2rem;
    gap: 1.2rem;
}

input, textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: .5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}

/* ***********************MEDIA QUERIES FOR MEDIUM DEVICES****************************** */
@media screen and (max-width: 1024px) {
  .contact__container.container{
      grid-template-columns: 1fr;
      grid-gap: 2rem;
      gap: 2rem;
  }
}

/* ***********************MEDIA QUERIES FOR SMALL DEVICES****************************** */
@media screen and (max-width: 600px) {
 .contact__container.container{
     width: var(--container-width-sm);
 }
}
